import "vant/es/pull-refresh/style/less";
import _PullRefresh from "vant/es/pull-refresh";
import "vant/es/list/style/less";
import _List from "vant/es/list";
import { computed, defineComponent, onMounted } from 'vue';
import { useStore } from 'vuex';
import { useRoute } from 'vue-router';
import { useRouteParams } from '@/hooks/useRouteParams';
import CustomizeWrapper from '@/components/customizeWrapper.vue';
import dayjs from 'dayjs';
export default defineComponent({
  components: {
    [_List.name]: _List,
    [_PullRefresh.name]: _PullRefresh,
    CustomizeWrapper
  },

  setup() {
    const store = useStore();
    const route = useRoute();
    onMounted(() => onRefresh());
    const {
      creditUserId
    } = useRouteParams();

    const onLoad = () => {
      let params = {
        data: {
          creditUserId
        }
      };
      store.dispatch('billModule/loadBills', params);
    };

    const onRefresh = () => {
      const {
        params: {
          creditUserId: creditUserId
        }
      } = route;
      let params = {
        data: {
          creditUserId
        }
      };
      store.dispatch('billModule/fetchBills', params);
    };

    const loading = computed({
      get() {
        return store.state.billModule.loading;
      },

      set(v) {
        store.state.billModule.loading = v;
      }

    });
    const refreshing = computed({
      get() {
        return store.state.billModule.refreshing;
      },

      set(v) {
        store.state.billModule.refreshing = v;
      }

    });

    const formateStr = v => {
      return dayjs(v).format('YYYY-MM-DD');
    };

    return {
      formateStr,
      bills: computed(() => store.state.billModule.bills),
      onLoad,
      loading,
      finished: computed(() => store.state.billModule.bills),
      refreshing,
      onRefresh
    };
  }

});